// Checks if URI is encoded
export const isEncoded = uri => {
    if (!uri) {
        return false;
    }
    return uri !== decodeURIComponent(uri);
};

// Completely decodes URI
export const fullyDecodeURI = uri => {
    while (isEncoded(uri)) {
        uri = decodeURIComponent(uri);
    }

    return uri;
};
